import React, { useState } from 'react';
import axios from 'axios';
import { toast, ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { Formik, Field, Form, ErrorMessage } from 'formik';
import * as Yup from 'yup';
import { Button, TextField, RadioGroup, FormControlLabel, Radio, Select, MenuItem, InputLabel, FormControl } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import './signup.css';

// Validation schema
const validationSchema = Yup.object().shape({
  username: Yup.string()
    .min(2, '  2 !')
    .max(20, '  20!')
    .matches(/^[a-zA-Z0-9]*$/, '   !')
    .required(' !'),
  name: Yup.string().required(' !'),
  password: Yup.string()
    .min(8, '  8 !')
    .matches(/^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#$%^&*])[A-Za-z\d!@#$%^&*]{8,}$/, ' , , ,   !')
    .required(' !'),
  confirmPassword: Yup.string()
    .oneOf([Yup.ref('password'), null], '  !')
    .required('  !'),
  phone: Yup.string()
    .matches(/^[0-9]{10,11}$/, ' 10  11  !')
    .required(' !'),
  company: Yup.string().required('   !'),
  gender: Yup.string().required(' !')
});

const SignUp = () => {
  const [isUsernameAvailable, setIsUsernameAvailable] = useState(null);
  const navigate = useNavigate();

  // Handle username availability check
  const checkUsername = async (username) => {
    try {
      const response = await axios.get(`/api/check-username/${username}`);
      setIsUsernameAvailable(response.data.available);
    } catch (error) {
      console.error(error);
      toast.error('    !');
    }
  };

  // Handle form submission
  const handleSubmit = async (values) => {
    const { username, name, password, phone, company, gender } = values;
    try {
      await axios.post('/api/auth/signup', {
        username,
        name,
        password,
        phone,
        company,
        gender
      });
      toast.success(' .  !');
      setTimeout(() => navigate('/login'), 2000);
    } catch (error) {
      toast.error(` : ${error.response.data.message}`);
    }
  };

  return (
    <Formik
      initialValues={{
        username: '',
        name: '',
        password: '',
        confirmPassword: '',
        phone: '',
        company: '',
        gender: ''
      }}
      validationSchema={validationSchema}
      onSubmit={handleSubmit}
    >
      {({ values, handleChange, handleBlur }) => (
        <div className="signup-wrapper">
          <ToastContainer />
          <Form autoComplete="off">
            <div className="input-forms">
              <div className="input-forms-item">
                <div className="input-label"></div>
                <Field
                  as={TextField}
                  name="username"
                  variant="outlined"
                  fullWidth
                  onBlur={() => checkUsername(values.username)}
                />
                <ErrorMessage name="username" component="div" className="error-message" />
                {isUsernameAvailable === true && <div className="availability-message">  .</div>}
                {isUsernameAvailable === false && <div className="availability-message error">   .</div>}
              </div>
              <div className="input-forms-item">
                <div className="input-label"></div>
                <Field
                  as={TextField}
                  name="name"
                  variant="outlined"
                  fullWidth
                />
                <ErrorMessage name="name" component="div" className="error-message" />
              </div>
              <div className="input-forms-item">
                <div className="input-label"></div>
                <Field
                  as={TextField}
                  name="password"
                  type="password"
                  variant="outlined"
                  fullWidth
                />
                <ErrorMessage name="password" component="div" className="error-message" />
              </div>
              <div className="input-forms-item">
                <div className="input-label"> </div>
                <Field
                  as={TextField}
                  name="confirmPassword"
                  type="password"
                  variant="outlined"
                  fullWidth
                />
                <ErrorMessage name="confirmPassword" component="div" className="error-message" />
              </div>
              <div className="input-forms-item">
                <div className="input-label"></div>
                <Field
                  as={TextField}
                  name="phone"
                  variant="outlined"
                  fullWidth
                />
                <ErrorMessage name="phone" component="div" className="error-message" />
              </div>
              <div className="input-forms-item">
                <div className="input-label">  </div>
                <FormControl variant="outlined" fullWidth>
                  <InputLabel></InputLabel>
                  <Field
                    as={Select}
                    name="company"
                    label=""
                    onBlur={handleBlur}
                  >
                    <MenuItem value=""></MenuItem>
                    <MenuItem value=""></MenuItem>
                    <MenuItem value=""></MenuItem>
                  </Field>
                </FormControl>
                <ErrorMessage name="company" component="div" className="error-message" />
              </div>
              <div className="input-forms-item">
                <div className="input-label"></div>
                <Field as={RadioGroup} name="gender">
                  <FormControlLabel value="male" control={<Radio />} label="" />
                  <FormControlLabel value="female" control={<Radio />} label="" />
                </Field>
                <ErrorMessage name="gender" component="div" className="error-message" />
              </div>
              <Button color="primary" variant="contained" fullWidth type="submit">
                
              </Button>
            </div>
          </Form>
        </div>
      )}
    </Formik>
  );
};

export default SignUp;
