import {jwtDecode} from 'jwt-decode'; // `jwt-decode`  default export 

export class jwtUtils {
  //   
  static isAuth(token) {
    if (!token) {
      return false;
    }
    try {
      const decoded = jwtDecode(token);
      //     
      const currentTime = Math.floor(Date.now() / 1000);
      return decoded.exp > currentTime;
    } catch (error) {
      console.error('Token decoding error:', error);
      return false;
    }
  }

  //   id 
  static getId(token) {
    try {
      const decoded = jwtDecode(token);
      console.log(decoded)
      // `jti`  `userId`    
      return decoded.username || decoded.sub; // `userId`  `sub`  
    } catch (error) {
      console.error('Token decoding error:', error);
      return undefined;
    }
  }
}
