import { createSlice } from '@reduxjs/toolkit';

//   
const initialState = {
  token: null,
};

// Slice 
const authSlice = createSlice({
  name: 'auth',
  initialState,
  reducers: {
    setToken(state, action) {
      state.token = action.payload; // payload   
    },
  },
});

//   
export const { setToken } = authSlice.actions;
export default authSlice.reducer;
